import React, {useState} from 'react'
import {useDispatch} from 'react-redux'
import {Link} from 'react-router-dom'
import {Counter} from '../Counter/counter'
import "./producttemplate.scss"
import {increment} from '../../Features/counter/cartQuantitySlice'

const ProductTemplate = ({
    ImageUrl,
    price,
    id,
    rating,
    title,
    quantity
}) => {

    const [count,
        setCount] = useState(0)
    const dispatch = useDispatch()

    return (
        <div className="product">
            <Link
                style={{
                backgroundImage: `url(${ImageUrl})`,
                backgroundSize: "cover",
                backgroundPosition: "center"
            }}
                to={`/products/${title}`}>
                <div>
                    <label>{title}</label>
                    <div>
                        <span>{`$ ${price}`}</span>
                        {/* <small>{rating}</small> */}
                    </div>
                </div>

            </Link>

            <div className='product-click'>
                <button
                    style={{
                    display: `${count < 1
                        ? "flex"
                        : "none"}`
                }}
                    onClick={() => {
                    setCount(count + 1)
                    dispatch(increment())
                    fetch(`http://localhost:3001/cart/`, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({count: count  +1, ImageUrl: ImageUrl, title: title, price: price, id: id})
                    })
                }}>Add to Cart</button>
                <Counter
                    quantity={quantity}
                    height={41}
                    count={count}
                    setCount={setCount}
                    id={id}
                    price={price}
                    title={title}
                    ImageUrl={ImageUrl}/>
            </div>
        </div>
    )
}

export default ProductTemplate