import React, {useEffect, useState} from 'react'
import {useDispatch} from 'react-redux'
import {increment, decrement} from '../../Features/counter/cartQuantitySlice'
import "./counter.scss"

export const Counter = ({quantity, height, count, setCount, id,price,ImageUrl,title}) => {
    // const [cart,
    //     setCart] = useState([])
    const dispatch = useDispatch()

    // useEffect(() => {
    //     const fetchCart = async() => {
    //         const resp = await fetch(`http://localhost:3001/cart`)
    //         const cart = await resp.json()
    //         setCart({
    //             quantity: cart.products[id - 1].quantity,
    //             price: cart.products[id - 1].price
    //         })

    //     }
    //     fetchCart()

    // }, [count]);
    //console.log(cart)
    return (
        <span
            className='counter'
            style={{
            height: `${height}px`,
            display: `${count >= 1
                ? "flex"
                : "none"}`
        }}>
            <button
                onClick={() => {
                if (count > 0) {
                    setCount(count - 1)
                    dispatch(decrement())
                    if (count < 1) {
                        fetch(`http://localhost:3001/cart/${id}`, {
                            method: 'DELETE',
                            headers: {
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({ count: count,ImageUrl: ImageUrl, title: title, price: price,id: `${id}`})
                        })
                    }
                }
            }}>-</button>
            <small>{count}</small>
            <button
                onClick={() => {
                if (count < quantity) {
                    setCount(count + 1)
                    dispatch(increment())
                    if (count > 0) {
                        fetch(`http://localhost:3001/cart/${id}`, {
                            method: 'PUT',
                            headers: {
                                'Content-Type': 'application/json'
                            },
                            body: JSON.stringify({})
                        })
                    }
                }
            }}>+</button>
        </span>
    )
}
